const prefix = '/filter/v1'

const callback = {
  onSuccess({ getState, dispatch, response }) {
    dispatch({ type: 'SET_DROPDOWN_OPTIONS', data: response.data.data, key: response.config.reduxSourceAction.key });
  },
  onError({ getState, dispatch, error }) {
    dispatch({ type: 'REQUEST_ERROR', data: error.response });
  }
}

export const getAirport = () => {
  return {
    type: 'GET_AIR_PORT',
    key: 'airPortOptions',
    payload: {
      request: {
        url: prefix + '/filterAirport'
      },
      options: callback
    }
  }
}

