const initState = {
  airPortOptions: [],
  airlineOptions: [],
  airTypeOptions: [],
  flightStatusOptions: []
}
export default (state = initState, action) => {
  switch (action.type) {
    case 'SET_DROPDOWN_OPTIONS':
      state[action.key] = action.data
      return {...state}
    default:
      return state
  }
}
